export const getPaginationOptions = ({
  page,
  visiblePages,
  itemsPerPage,
  totalItems,
} = {}) => ({
  totalItems,
  itemsPerPage,
  page,
  visiblePages,
  centerAlign: true,
  usageStatistics: false,
  template: {
    page: '<a href="#" class="tui-page-btn">{{page}}</a>',
    currentPage:
      '<strong class="tui-page-btn tui-is-selected">{{page}}</strong>',
    moveButton: ({ type }) =>
      `<a href="#" class="tui-page-btn tui-${type}">` +
      `<svg class="pagination-${
        type === 'next' ? 'right' : 'left'
      }-icon"><use href="${spriteUrl}#${
        type === 'next' ? 'right' : 'left'
      }-arrow-icon"></use></svg>` +
      '</a>',
    disabledMoveButton: ({ type }) =>
      `<span class="tui-page-btn tui-is-disabled tui-${type}">` +
      `<svg class="icon-disabled pagination-${
        type === 'next' ? 'right' : 'left'
      }-icon"><use href="${spriteUrl}#${
        type === 'next' ? 'right' : 'left'
      }-arrow-icon"></use></svg>` +
      '</span>',
    moreButton:
      '<a href="#" class="tui-page-btn tui-{{type}}-is-ellip">' +
      '<span class="tui-ico-ellip">...</span>' +
      '</a>',
  },
});

export const getVisiblePages = width => {
  if (width < 768) {
    return 3;
  } else if (width >= 768 && width < 1440) {
    return 4;
  }
  return 5;
};

export const formatDuration = ms => {
  if (!ms || isNaN(ms)) return 'N/A';
  const totalSeconds = Math.floor(ms / 1000);
  const min = Math.floor(totalSeconds / 60);
  const sec = totalSeconds % 60;
  return `${min}:${sec.toString().padStart(2, '0')}`;
};

export function updateArrowVisibility(swiper) {
  const prevBtn = swiper.el.querySelector('.swiper-button-prev');
  const nextBtn = swiper.el.querySelector('.swiper-button-next');

  if (swiper.isBeginning) {
    prevBtn.classList.add('swiper-button-disabled');
  } else {
    prevBtn.classList.remove('swiper-button-disabled');
  }

  if (swiper.isEnd) {
    nextBtn.classList.add('swiper-button-disabled');
  } else {
    nextBtn.classList.remove('swiper-button-disabled');
  }

  if (swiper.slides.length <= swiper.params.slidesPerView) {
    prevBtn.classList.add('swiper-button-disabled');
    nextBtn.classList.add('swiper-button-disabled');
  }
}

export function toggleTheme() {
  const current = document.body.getAttribute('data-theme');
  const next = current === 'dark' ? 'light' : 'dark';
  document.body.setAttribute('data-theme', next);

  saveTheme(next);
}

export function handleScrollNavigation() {
  window.addEventListener('scroll', () => {
    const headerHeight = refs.headerEl.offsetHeight;
    const sectionTop = refs.artistsEl.offsetTop;

    if (window.scrollY >= sectionTop - headerHeight) {
      refs.headerEl.style.transition = 'background-color 0.4s ease';
    } else {
      refs.headerEl.style.transition = 'none';
    }
  });
}
